import route from "@/common/public.js"
import uniIcon from "@/components/uni-icon/uni-icon.vue"
import uniPopup from '@/components/uni-popup/uni-popup.vue'
export default{
	components: {uniIcon,uniPopup},
	data(){
		return {
			scrollTop: 0,
				old: {
					scrollTop: 0
			},
			subaccount:false,
			bianJi:true,
			sonName:"", //获取用户输入的用户名
			sonPhone:"", //获取用户输入的电话
			sonPwd:"",  //获取用户输入的密码
			Signboard:'',
			Signguid:'',
			subaccountInfo:'', //定义this.subaccountInfo 来存 res.data.subaccount;
			subid: '' ,//用来存items.uid
			inputName: '',				
			inputPhone: '',
			sacntList:'', //将原数据存起来用作判断数据是否有被改动
			showMask:false, //遮罩层的显示隐藏
		}
	},
	methods:{
		back(){
			uni.navigateBack({
				delta:1
			})
		},			// 页面滑动
		// 页面滑动
		scroll: function(e) {
			this.old.scrollTop = e.detail.scrollTop
		},
		// 点击修改图标触发的事件 编辑地址
		composeTap(e){
			this.sacntList=e;
			this.subaccount = true;
			this.bianJi = false;
			this.showMask=true;
			this.subid = e.uid;
			this.inputName = e.uname;
			this.inputPhone = e.telephone;
			this.sonName = e.uname;
			this.sonPhone = e.telephone;
			uni.setNavigationBarTitle({
				title: '编 辑 子 账 号'
			})
		},
		// 删除地址的确定按钮
		dlSubaccounts(){
			let _that = this;
			uni.showModal({
				title:"提示",
				content:"是否删除此条账号信息？",
				confirmColor:'#E93B3D',
				success:function(res){
					if(res.confirm){
						_that.subAdsYesFun();
						uni.setNavigationBarTitle({
							title: '子 账 号  管 理'
						})
					}else if(res.cancel){
						uni.setNavigationBarTitle({
							title: '子 账 号  管 理'
						})
					}
				}
			})
		},
		// 删除地址 弹出层里的确定按钮
		/* subAdsYes(e){
			this.subAdsYesFun();
			uni.setNavigationBarTitle({
				title: '子 账 号  管 理'
			})
		}, */
		// 删除地址 弹出层里的 取消按钮
		subAdsNo(e){
			this.subAds = false;
		},
		//获取用户输入的数据 1.用户名 2.电话 3.密码
		name:function(evt){
			this.sonName=evt.target.value;
		},
		phone:function(evt){
			this.sonPhone=evt.target.value;
		},
		password:function(evt){
			this.sonPwd=evt.target.value;
		}, 
		//点击修改页面的确定按钮触发的事件
		upSubaccounts(evt){
			//对表单进行验证
			if(this.sonName=="" || this.sonPhone==""){
				uni.showToast({
					title:'请将信息填写完整,不允许为空',
					icon:'none'
				})
			}else if(!RegExp(/^[\u4E00-\u9FA5A-Za-z0-9]+$/).test(this.sonName)){
				uni.showToast({
					title:"用户名只能包含中文，字母，数字",
					icon: "none"
				})
			}else if(!RegExp(/^(13[0-9]|14[5|7]|15[0|1|2|3|5|6|7|8|9]|18[0|1|2|3|5|6|7|8|9]|17[0|1|2|3|5|6|7|8|9])\d{8}$/).test(this.sonPhone)){
				uni.showToast({
					title:"手机号码格式不正确",
					icon: "none"
				})
			}else if(this.sonPwd != "" && !RegExp(/^[A-Za-z0-9]{5,16}$/).test(this.sonPwd)){
				uni.showToast({
					title:"密码只能由字母数字组成,长度不能小于6位大于16位",
					icon: "none"
				})
			}else{
				//调updSubAccount(修改子账户)的接口
				uni.request({
					url:route.variable+'/mobile/Subaccount/updSubAccount',
					method:'POST',
					data:{
						Ident_Signboard: this.Signboard,
						Ident_Signguid: this.Signguid,
						uid: this.subid,
						uname:this.sonName,
						telephone:this.sonPhone,
						password:this.sonPwd
					},
					success: (res) => {
						if (route.publicIf(res.data.status) == false){
							return false;
						}
						if(res.data.status==0){
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
							this.bianJi = true;
							this.subaccount = false;
							this.showMask=false;
							this.onSubaccount();
							/* uni.setNavigationBarTitle({
								title: '子 账 号  管 理'
							}) */
						}else{
							uni.showToast({
								title:res.data.message,
								icon:'none'
							})
						}
					},
					fail:(res)=>{
						uni.hideLoading()
						uni.showToast({title: '数据加载失败'+'错误码201',icon:"none"});
						console.log("fail: "+JSON.stringify(e));
					}
				})
			}
		},
		// 删除子账号 函数
		subAdsYesFun(evt){
			uni.request({
				url:route.variable+'/mobile/Subaccount/delSubAccount',
				method:'GET',
				data:{
					uid:this.subid,
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid
				},
				success: (res) => {
					if (route.publicIf(res.data.status) == false){
						return false;
					}
					if(res.data.status==0){
						uni.showToast({
							title:res.data.message,
							icon:'none'
						})
						uni.navigateTo({
							url:'../user/subaccount'
						})
					}else{
						uni.showToast({
							title:'子账户删除失败',
							icon:'none'
						})
					}
				},
				fail:(res)=>{
					uni.hideLoading()
					uni.showToast({title: '删除子账号失败'+'错误码201',icon:"none"});
					console.log("fail: "+JSON.stringify(e));
				}
			})
		},
		//获取子账号信息
		onSubaccount(){
			uni.request({ 
				//获取子账号信息
				url: route.variable+'/mobile/Subaccount/getSubAccount',  //调用子账号接口的地址
				method: 'GET',
				data: {
					Ident_Signboard: this.Signboard,
					Ident_Signguid: this.Signguid
				},
				success: (res) => {
					if (route.publicIf(res.data.status) == false){
						return false;
					}
					if(res.data.status == 0){
						this.subaccountInfo = res.data.subaccount;
					}
				},
				fail:(res)=>{
					uni.hideLoading()
					uni.showToast({title: '数据加载失败'+'错误码201',icon:"none"});
					console.log("fail: "+JSON.stringify(e));
				}
			})
		}
	},
	
	onLoad:function(){
		var jsonList=uni.getStorageSync("jsonList");
		var data = JSON.parse(jsonList); //JSON字符串转对象
		this.Signboard = data.Ident_Signboard;
		this.Signguid = data.Ident_Signguid;
	},
	onShow() {
		this.onSubaccount();
	},
	onBackPress(options) {
		let _that=this;
		if(this.showMask) {
			if(this.showMask == true){
				if(this.sacntList.uname != this.inputName || this.sacntList.telephone != this.inputPhone){
					uni.showModal({
						title:"提示",
						content:"是否保存已修改的信息",
						confirmColor:'#E93B3D',
						success:function(res){
							if(res.confirm){
								_that.upSubaccounts();
								/* this.isActive = true;
								this.isActives = false; */
								uni.setNavigationBarTitle({
									title: '子 账 号 管 理'
								})
							}else if(res.cancel){
								uni.setNavigationBarTitle({
									title: '子 账 号 管 理'
								})
							}
						}
					})
					/* this.showMask = false;
					return true; */
				}else{
					this.bianJi = true;
					this.subaccount = false;
					uni.setNavigationBarTitle({
						title: '子 账 号 管 理'
					})
				}
				this.showMask = false;  
				return true;  
			}
	  }else{
		  if (options.from === 'navigateBack'||options.from === 'backButton') {
			  return false;  
		  }
		  this.bianJi = true;
		  this.subaccount = false;
		  this.showMask = false;  
		  this.back();  
		  return true;  
	  }
	},
}
